import React from "react"
import { rhythm } from "../../utils/typography"
import { Maybe, SitePageContextPrevious } from "../../graphql-types"
import { BlogLink } from "../atoms/blog-link"

interface Props {
  previous: Maybe<SitePageContextPrevious> | undefined
  next: Maybe<SitePageContextPrevious> | undefined
}

export const PostNavigator = (props: Props) => {
  const { previous, next } = props

  return (
    <ul
      style={{
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "space-between",
        listStyle: "none",
        marginTop: rhythm(1),
        padding: 0,
      }}
    >
      <li>
        {previous && (
          <BlogLink to={previous.fields!.slug!} rel="prev">
            ← {previous.frontmatter!.title}
          </BlogLink>
        )}
      </li>
      <li>
        {next && (
          <BlogLink to={next.fields!.slug!} rel="next">
            {next.frontmatter!.title} →
          </BlogLink>
        )}
      </li>
    </ul>
  )
}
