import { graphql, PageRendererProps } from "gatsby"
import React, { useEffect, useRef } from "react"

import { Bio } from "../components/molecules/bio"
import { Layout } from "../components/organisms/layout"
import { SEO } from "../components/molecules/seo"
import { Query, SitePageContext } from "../graphql-types"
import { Divider } from "../components/atoms/divider"
import { Date } from "../components/atoms/date"
import { PostNavigator } from "../components/molecules/post-navigator"

interface Props extends PageRendererProps {
  pageContext: SitePageContext
  data: Query
}

const BlogPost = (props: Props) => {
  const data = props.data!
  const post = data.markdownRemark!
  const excerpt = post.excerpt!
  const frontmatter = post.frontmatter!
  const html = post.html!
  const siteTitle = data.site!.siteMetadata!.title!
  const { previous, next } = props.pageContext
  const commentsRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const script = document.createElement("script")
    script.src = "https://utteranc.es/client.js"
    script.crossOrigin = "anonymouse"
    script.async = true
    script.setAttribute("issue-term", post.frontmatter!.title as string)
    script.setAttribute("repo", "sungjunyoung/blog-comments")
    script.setAttribute("rethemepo", "github-light")
    commentsRef.current!.appendChild(script)
  }, [])

  return (
    <Layout location={props.location} title={siteTitle}>
      <SEO
        title={frontmatter.title!}
        description={frontmatter.description || excerpt}
      />
      <h1>{post.frontmatter!.title}</h1>
      <br/>
      <Date>{frontmatter.date}</Date>
      <div dangerouslySetInnerHTML={{ __html: html }} />
      <Divider />
      <Bio />
      <div className="comments-wrapper" ref={commentsRef}></div>
      <PostNavigator previous={previous} next={next} />
    </Layout>
  )
}

export default BlogPost

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
      }
    }
  }
`
